}

.header__logo {
  z-index: 11;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.header__logo:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.header__icon-logo {
  fill: currentColor;
  width: 47px;
  height: 30px;
}

@media screen and (min-width: 321px) {
  .header__icon-logo {
