    height: 68px;
  }
}

.header__burger {
  z-index: 11;
  width: 30px;
  height: 20px;
  display: block;
  position: relative;
}

.header__burger:after, .header__burger:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #ff7400;
  position: absolute;
  left: 0;
}

.header__burger:before {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  top: 0;
}

.header__burger:after {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  bottom: 0;
}

.header__burger.active:before {
  top: 9px;
  transform: rotate(45deg);
}

.header__burger.active:after {
  bottom: 9px;
  transform: rotate(-45deg);
}

.header__burger span {
  width: 100%;
  height: 2px;
  background-color: #ff7400;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 9px;
}

.header__burger.active span {
  transform: scale(0);
}

@media screen and (min-width: 768px) {
  .header__burger {
    display: none;
  }
}

.header__nav {
  width: 100vw;
